/* Modal.css */
.ModalContent {
  background-color: #ffffff;
  color: black;
  width: 50vw;
  height: 80vh;
  margin: auto;
  position: relative;
  z-index: 1000000;
  border: none;
  align-content: center;
  padding: 1vw;
  overflow: scroll;
  box-shadow: 0px 0px 15px 0px black;
  outline: none;
}

.ModalTitle {
  font-size: 16px;
  line-height: 32px;
  display: inline;
  margin: 0 10px 0 0px;
}
.ModalText {
  font-size: 16px;
  line-height: 32px;
  display: inline;
  margin: 0 10px 0 0px;
}
#ModalDialogue {
  width: 100%;
  font-size: 60px;
  line-height: 1.5;
  margin: 0;
  border-bottom: 0px solid rgb(50, 50, 50);
  padding: 100px 0px 20px 0px;
}
.ModalOverlay {
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1000000;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.ButtonWrap {
  position: absolute;
  /* transform: translate(-50%, 0px); */
  right: 0;
  width: 360px;
  height: 103px;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px;
  top: 0px;
}

.CloseButton {
  padding: 10px;
  width: 150px;
  height: 60px;
  font-size: 20px;
  border: none;
  color: white;
  background-color: #5c5c5c;
  cursor: pointer;
}
.CloseButton:hover {
  background-color: var(--tanColor);
}

.PrintButton {
  padding: 10px;
  color: white;
  background-color: black;
  width: 150px;
  height: 60px;
  font-size: 20px;
  border: none;
  cursor: pointer;
  margin-right: 30px;
}
.PrintButton:hover {
  background-color: var(--tanColor);
}

.VimeoAndComment {
  /* display: grid; */
  grid-auto-columns: 1fr;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 0px 30px;
  grid-template-areas: " vimeo comment";
}

.vimeoWrap {
  grid-area: vimeo;
  width: 100%;
  height: 100%;
  position: relative;
}

.vimeoVideo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.commentWrap {
  position: absolute;
  grid-area: comment;
  padding: 30px 10px 50px 10px;
  top: 0;
}

.ModalComment {
  color: white;
  font-size: 16px;
  line-height: 32px;
  margin: 0;
}

#printable-modal-content {
  padding: 20px;
}
