.CtrC .subtitle {
  color: #353535;
  top: -122px;
  left: 43px;
  padding: 0 6px;
  font-size: 13px;
  position: absolute;
}
.PeopleSection {
  width: 125px;
  height: 328px;
  top: 60px;
  border-radius: 12px;
  position: relative;
  cursor: pointer;
}
.FilterActor {
  grid-area: FilterActor;
}
.FilterDirector {
  grid-area: FilterDirector;
}
.FilterSource {
  grid-area: FilterSource;
}
.FilterScenario {
  grid-area: FilterScenario;
}
.FilterAdaptation {
  grid-area: FilterAdaptation;
}
.FilterActor,
.FilterDirector,
.FilterSource,
.FilterScenario,
.FilterAdaptation {
  position: absolute;
  width: 105px;
  font-size: 14px;
  top: 70px;
  height: 140px;
  text-align: center;
  align-items: center;
  justify-content: center;
  display: flex;
}

.PeopleWrap {
  position: relative;
  overflow-y: scroll;
  width: 125px;
  top: 0px;
  height: 320px;
  margin: 0px 0px 0px 0px;
  text-align: center;
  /* background: var(--gradient2); */
  box-shadow: 0px 0px 6px -1px rgba(0, 0, 0, 0.7) inset;
  border-radius: 8px;
  left: 10px;
}

.PeopleContainer::-webkit-scrollbar {
  width: 10px;
  box-sizing: border-box;
}
.PeopleContainer::-webkit-scrollbar-track {
  background-color: black;
}

.PeopleContainer::-webkit-scrollbar-thumb {
  background: rgb(0, 0, 0);
  border-radius: 2px;
  border-top: 5px solid rgb(85, 85, 85);
  border-bottom: 5px solid rgb(85, 85, 85);
  background-image: url(../../imgs/scroll_bg.png);
  background-size: contain;
}

.PeopleContainer::-webkit-scrollbar-thumb:hover {
  background: #999999;
}
/* 체크박스 스타일 */
.checkbox-div {
  cursor: pointer;
  border-bottom: 0px solid #464646;
  background-color: #eee1c1;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75) inset;
}
.PeopleInput .Param {
  cursor: pointer;
  font-size: 16px;
  line-height: 55px;
}

.checkbox-div:hover {
  background: linear-gradient(
    0deg,
    #eee1c1 0%,
    #ffe300 10%,
    #ffecbe 90%,
    #ffe300 100%
  );
}
.checkbox-div.checked {
  background: linear-gradient(
    0deg,
    #eee1c1 0%,
    #ffe300 10%,
    #ffecbe 90%,
    #ffe300 100%
  );
  box-shadow: 0px 0px 10px #ffe300;
  color: black;
  cursor: pointer;
  animation: flicker 0.2s infinite 0.3s;
  border-bottom: 6px solid #997f3f;
}
.checkbox-label.checked .Param {
  color: black;
}

/* 체크박스 숨기기 */
.checkbox-div.checked .checkbox-label input {
  display: none;
}
.hidden-checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

@keyframes flicker {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
@keyframes light-off {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
}
