.isMouseActive {
  /* Styles for isMouseActive class */
}

.scaleUp {
  transform: scale(1.1);
}
.style-1 {
  font-family: "CBNUJIKJI", serif;
}
.style-2 {
  font-family: "JSongMyung-Regular-KO", serif;
  font-weight: normal;
}
