:root {
  --bgColor: #eeeeea;
  --pinkColor: #ffb9b9;
  --tanColor: #a38852;
  --darkgrayColor: rgb(50, 50, 50);
  --lightgrayColor: #b3b3b3;
  --beigeColor: #eee1c1;
  --gradient: linear-gradient(
    180deg,
    rgba(161, 134, 79, 1) 0%,
    rgba(217, 199, 164, 1) 77%,
    rgba(217, 199, 164, 1) 100%
  );
  --gradient2: linear-gradient(
    180deg,
    rgba(161, 134, 79, 1) 50%,
    rgba(217, 199, 164, 1) 100%
  );
}
* {
  scrollbar-width: none;
}
body {
  /* background-color: var(--darkgrayColor); */
}

.VisitorsGuide {
  z-index: 999999;
  position: fixed;
  font-size: 16px;
  color: black;
  top: 390px;
  left: 920px;
  animation: bounce0 4s ease infinite;
}
.VisitorsGuideClick {
  z-index: 999999;
  position: fixed;
  font-size: 16px;
  color: rgb(151, 0, 0);
  top: 20px;
  left: 50px;
  animation: blink 2s ease infinite;
}
.VisitorsGuideClickPeople {
  z-index: 999999;
  position: fixed;
  font-size: 16px;
  color: rgb(151, 0, 0);
  top: 20px;
  left: 1150px;
  animation: blink 2s ease infinite;
}

* {
  font-weight: 600;
  scrollbar-color: #8f8f8f #000;
}

.Controller {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 420px;
  z-index: 1;
}
/*  *  *  *  *  *  *  *  *  */
.ContainerCtr {
  background: var(--gradient);
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-columns: 1fr 0.5fr 1.1fr 0.8fr;
  grid-template-rows: 1fr;
  gap: 0px 8px;
  grid-template-areas: "CtrA CtrB CtrC CtrD";
  height: 100%;
  /* box-shadow: inset 0 30px 8px rgb(79, 79, 79), 0 9px 10px rgb(180, 180, 180, 1); */
}
.List {
  position: relative;
  top: 420px;
  z-index: 0;
  background-color: navy;
  color: white;
  font-size: 2em;
  font-weight: 600;
  height: 72vh - 26px;
  overflow-y: hidden;
}
/*  *  *  *  * Table *  *  *  *  */
.TableContainer {
  top: 420px;
  height: 100%;
  position: relative;
}
.TheadTable {
  top: 435px;
  position: fixed;
  z-index: 9999;
  width: 100vw;
  color: rgb(50, 50, 50);
  height: 35px;
  /* border-top: 4px solid #777777; */
}
.TheadTable th {
  background: rgba(217, 199, 164, 1);
  color: var(--tanColor);
  top: -17px;
  font-size: 18px;
}
.TBodyTable {
  position: relative;
  margin: 30px 0 0 0;
  height: 100%;
  border-collapse: collapse;
}
.index {
  height: 32px;
  font-size: 0.6em;
  padding: 0px;
  position: relative;
  text-align: center;
  border-radius: 0px 0px 40px 15px;
  /* box-shadow: inset 0 -2px 2px rgb(55, 55, 55); */
}
#indexYear {
  width: 3.7%;
}
#indexMovie {
  width: 12.2%;
}
#indexDialogue {
  width: 43%;
}
#indexActor {
  width: 8.2%;
}
#indexDirector {
  width: 3.1%;
}
#indexSource {
  width: 3.1%;
}
#indexScenario {
  width: 3.1%;
}
#indexAdaptation {
  width: 3.1%;
}

.ListData {
  font-size: 1em;
  line-height: 1.89em;
  /* padding: 0px 10px; */
  padding: 0px;
  color: #181010;
  position: relative;
  border-bottom: 1px solid var(--lightgrayColor);
  text-align: center;
  word-break: keep-all;
  padding: 20px 0px 20px 0px;
  /* border-right: 1px solid; */
}
.ListContainer tr:hover {
  background-color: var(--lightgrayColor);
  cursor: pointer;
}
.ListContainer tr:hover > .ListData {
  color: black;
}
.Year {
  width: 5vw;
}
.Movie {
  width: 15vw;
}
.Dialogue {
  width: 55vw;
}
.Actor {
  width: 10vw;
}
.Director {
  width: 4vw;
}
.Source {
  width: 4vw;
}
.Scenario {
  width: 4vw;
}
.Adaptation {
  width: 4vw;
}

.DataResult {
  color: yellow;
  text-align: center;
  padding: 10px 0 10px;
  font-size: 16px;
  line-height: 32px;
  background-color: #292929;
  position: fixed;
  width: 100vw;
  margin: 0;
  top: 450px;
  z-index: 99999;
}

@keyframes bounce0 {
  0% {
    opacity: 0;
    transform: translateY(0);
  }
  20%,
  50%,
  80% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
