.KeywordSection {
  grid-area: Character;
  width: 460px;
  height: 330px;
  box-sizing: border-box;
  display: flex;
  overflow-x: hidden;
  overflow-y: hidden;
  padding: 6px 3px 0px 3px;
  margin: 56px 0px 0px 10px;
  border-radius: 12px;
}
.KeywordWrap {
  width: 450px;
  height: 317px;
  /* background-color: var(--darkgrayColor); */
  box-sizing: border-box;
  border-radius: 12px;
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  padding: 2px;
  gap: 0px 3px;
}
.Keyword {
  /* background: var(--gradient2);
  border: 1px solid rgba(161, 134, 79, 1); */
  border-radius: 20px;
  position: relative;
  width: 60px;
}
.KeywordSection .subtitle {
  color: #353535;
  top: 18px;
  left: 18px;
  position: absolute;
  padding: 0 6px;
  font-size: 13px;
}

.Keyword .Param {
  font-size: 0.9em;
  line-height: 1.1em;
  width: 20px;
  display: block;
  border-radius: 0px;
  text-align: center;
  background: #eee1c1;
  top: 170px;
  left: 35%;
  padding: 5px 0px;
  font-size: 15px;
  line-height: 1.3;
  transform: translateX(-50%);
  margin: 0px;
  position: relative;
  cursor: pointer;
  font-family: "Gothic A1", sans-serif;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5) inset;
}
.Keyword .Scope {
  font-size: 0.7em;
  font-weight: 800;
  padding: 2px 6px;
  margin: 7px;
  text-align: center;
  background: rgb(20, 20, 20);
  color: rgb(114, 114, 114);
  border-radius: 5px;
}
.KeywordInput {
  display: none;
}
.KeywordLabel {
  display: block;
  width: 94px;
  height: 35px;
  cursor: pointer;
  border-radius: 8px;
  transition: 250ms;
  transform: rotate(-90deg);
  top: 26px;
  left: -25px;
  position: relative;
}

.KeywordLabel::after {
  position: absolute;
  content: "";
  top: 0px;
  left: -80px;
  transition: 250ms ease-in-out;
  background: linear-gradient(
    90deg,
    rgb(126, 126, 126) 0%,
    rgb(121, 121, 121) 10%,
    rgba(0, 0, 0, 1) 90%,
    rgb(162, 162, 162) 100%
  );
  height: 2em;
  width: 2.5em;
  border-left: 10px solid black;
  border-right: 8px solid black;
  height: 32px;
  width: 38px;
  border-left: 8px solid #000000;
  border-right: 8px solid #000000;
}

.Keyword .Param:hover {
  background: var(--tanColor);
}

.KeywordLabel::before {
  position: absolute;
  content: "";
  width: 185px;
  height: 7px;
  top: 12px;
  left: -100px;
  border-radius: 15px;
  transition: 250ms ease-in-out;
  background: black;
  box-shadow: 0 0.08em 0.15em -0.1em rgba(0, 0, 0, 0.5) inset,
    0 0.05em 0.08em -0.01em rgba(255, 255, 255, 0.7),
    0 0 0 0 rgb(113 113 113 / 70%) inset;
}

input:checked + .KeywordLabel::before {
  box-shadow: 0 0.08em 0.15em -0.1em rgba(0, 0, 0, 0.5) inset,
    0 0.05em 0.08em -0.01em rgba(255, 255, 255, 0.7),
    10em 0 0 0 var(--tanColor) inset;
  animation: flicker 0.3s infinite 0.3s;
}

input:checked + .KeywordLabel::after {
  left: 34px;
}
.KeywordWrap::-webkit-scrollbar {
  height: 10px;
  box-sizing: border-box;
}
.KeywordWrap::-webkit-scrollbar-track {
  /* box-shadow: inset 0 0 5px grey; */
}

.KeywordWrap::-webkit-scrollbar-thumb {
  background: rgb(0, 0, 0);
  border-radius: 2px;
  border-right: 5px solid rgb(85, 85, 85);
  border-left: 5px solid rgb(85, 85, 85);
  background-image: url(../../imgs/scroll_bg.png);
  background-size: contain;
}

.KeywordWrap::-webkit-scrollbar-thumb:hover {
  background: #999999;
}
